.projectsBlock {
    min-height: 50vh;
    background-color: #1f1f20;
    padding: 50px 0;
    .projectsContainer {
        flex-direction: column;
        justify-content: flex-start;
        .projects {
            width: 100%;
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;
            @media screen and (max-width: 725px) {
                justify-content: center;
            }
        }
    }
}





